import removeClassSubmitForm from './remove-class-submit-form';
import onSuccess from './success-submit-form';
import onError from './error-submit-form';


$('.telegram-form').on('submit', function (event) {

    event.stopPropagation();
    event.preventDefault();

    let form = this,
        submit = $('.submit', form),
        data = new FormData(),
        files = $('input[type=file]')


    $('.submit', form).val('...');
    $('input, textarea', form);

    //.attr('disabled','')

    data.append( '', 		            $('[name="name"]', form).val() );
    data.append( '', 		        $('[name="phone"]', form).val() );
    data.append( ' ', 		$('[name="city"]', form).val() );
    data.append( '', 		        $('[name="text"]', form).val() );
    // data.append( 'file', 		$('[name="file"]', form).val() );
   

    files.each(function (key, file) {
        let cont = file.files;
        if ( cont ) {
            $.each( cont, function( key, value ) {
                data.append( key, value );
            });
        }
    });
    
    $.ajax({
        url: '../../ajax.php',
        type: 'POST',
        data: data,
        cache: false,
        dataType: 'json',
        processData: false,
        contentType: false,
        xhr: function() {
            let myXhr = $.ajaxSettings.xhr();

            if ( myXhr.upload ) {
                myXhr.upload.addEventListener( 'progress', function(e) {
                    if ( e.lengthComputable ) {
                        let percentage = ( e.loaded / e.total ) * 100;
                            percentage = percentage.toFixed(0);
                        $('.submit', form)
                            .html( percentage + '%' );
                    }
                }, false );
            }

            return myXhr;
        },
        error: function( jqXHR, textStatus ) {
            //   
            if (textStatus === 'error') {
                onError()
            } else return
        },
        complete: function(jqXHR, textStatus) {
            //   -     
            if (textStatus === 'error') {
                return
            }
            form.reset() 
            removeClassSubmitForm()
            onSuccess()
        }
    });

    return false;
});